@import "../../assets/styles/functions.scss";


.post-page {
    position: relative;
    margin: 5% 16.5%;

    .post-page__close-btn {
        position: absolute;
        top: -5%;
        right: -10.2%;

        button {
            background: transparent;
            border: none;
            cursor: pointer;
            svg{
                width: calc-between-width(1920, 56, 3840, 112);
                height: calc-between-width(1920, 56, 3840, 112);
            }
        }
    }

    .post-page__header {
        position: relative;
        display: flex;
        align-items: flex-end;
        background: #E9ECEF;
        width: 100%;
        height: calc-between-width(1920, 580, 3840, 1160);
        border-radius: calc-between-width(1920, 20, 3840, 40);
        margin-bottom: calc-between-width(1920, 56, 3840, 112);

        .post-page__header__container {
            display: flex;
            align-items: center;
            border-radius: calc-between-width(1920, 16, 3840, 32);
            width: 100%;
            height: calc-between-width(1920, 274, 3840, 584);

            .post-page__header__blur {
                position: absolute;
                background: #CDD8DA73;
                bottom: 0;
                width: 100%;
                height: calc-between-width(1920, 274, 3840, 584);
                z-index: 1;
                border-radius: calc-between-width(1920, 16, 3840, 32);
                overflow: hidden;
                backdrop-filter: blur(4px);
            }

            .post-page__header__info {
                position: relative;
                z-index: 2;
                width: 100%;
                height: calc-between-width(1920, 182, 3840, 364);
                margin-left: calc-between-width(1920, 60, 3840, 120);

                .post-page__header__icons {
                    svg {
                        width: calc-between-width(1920, 25, 3840, 50);
                        height: calc-between-width(1920, 25, 3840, 50);
                        margin-right: 1.5rem;
                    }

                }

                .post-page__header__title {
                    margin: calc-between-width(1920, 12, 3840, 24) 0;
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 32, 3840, 64);
                    font-weight: 600;
                    line-height: calc-between-width(1920, 48, 3840, 96);
                    letter-spacing: 0em;
                    text-align: left;

                    max-width: calc-between-width(1920, 820, 3840, 1640);
                    color: #0E0E0F;
                }

                .post-page__header__creator {
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 16, 3840, 32);
                    font-weight: 500;
                    line-height: calc-between-width(1920, 24, 3840, 48);
                    letter-spacing: 0em;
                    text-align: left;
                    color: #0E0E0F;
                }
            }
        }



    }

    .post-page__content {

        h3 {
            margin-top: calc-between-width(1920, 40, 3840, 80);
            margin-bottom: calc-between-width(1920, 24, 3840, 48);
            font-family: 'Poppins';
            font-size: calc-between-width(1920, 24, 3840, 48);
            font-weight: 600;
            line-height: calc-between-width(1920, 30, 3840, 60);
            letter-spacing: 0em;
            text-align: left;
            text-transform: uppercase;
            color: #25A8CF;
        }

        p {
            font-family: 'Poppins';
            font-size: calc-between-width(1920, 18, 3840, 36);
            font-weight: 400;
            line-height: calc-between-width(1920, 28, 3840, 56);
            letter-spacing: 0em;
            text-align: justify;
            color: white;
        }
    }
}