@import '../../assets/styles/functions.scss';


.help-center__faq {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 5% 10%;
    margin-bottom: 0;

    .help-center__faq__title {
        position: relative;
        width: calc-between-width(1920, 453, 3840, 906);
        font-family: 'Poppins';
        font-size: calc-between-width(1920, 48, 3840, 96);
        font-weight: 500;
        line-height: calc-between-width(1920, 72, 3840, 144);
        letter-spacing: 0em;
        text-align: center;
        text-transform: uppercase;
        color: white;
        margin-bottom: calc-between-width(1920, 100, 3840, 200);

        span {
            position: absolute;
            width: calc-between-width(1920, 280, 3840, 580);
            height: calc-between-width(1920, 55, 3840, 110);
            bottom: 5%;
            left: 2%;
            border-radius: calc-between-width(1920, 60, 3840, 120);
            border: 1.5px solid rgba(37, 168, 207, 1)
        }
    }

    .help-center__faq__question-group {
        width: 100%;

        .help-center__faq__question-group__title {
            font-family: 'Poppins';
            font-size: calc-between-width(1920, 24, 3840, 48);
            font-weight: 500;
            line-height: calc-between-width(1920, 36, 3840, 72);
            letter-spacing: 0em;
            text-align: left;
            text-transform: uppercase;
            color: white;
            margin-bottom: calc-between-width(1920, 24, 3840, 48);
        }

        .help-center__faq__question {
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 100%;
            min-height: calc-between-width(1920, 85, 3840, 170);
            background: #19151E99;
            border-radius: 16px;
            margin-bottom: calc-between-width(1920, 16, 3840, 32);
            &:last-child{
                margin-bottom: calc-between-width(1920, 48, 3840, 96);
            }

            .help-center__faq__question__title {
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: space-between;

                margin: 0 calc-between-width(1920, 42, 3840, 84);
                padding-top: calc-between-width(1920, 40, 3840, 80) ;
                padding-bottom: calc-between-width(1920, 5, 3840, 10);
                
                .help-center__faq__question__title__decoration-text {
                    display: flex;
                    margin-bottom: calc-between-width(1920, 30, 3840, 60);
                    .help-center__faq__question__title__decoration {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: calc-between-width(1920, 40, 3840, 80);
                        height: calc-between-width(1920, 40, 3840, 80);
                        background: #25A8CF;
                        border-radius: 8px;
                        margin-right: calc-between-width(1920, 24, 3840, 48);

                        svg {
                            width: calc-between-width(1920, 20, 3840, 40);
                            height: calc-between-width(1920, 20, 3840, 40);
                        }
                    }

                    .help-center__faq__question__title__text {
                        font-family: 'Poppins';
                        font-size: calc-between-width(1920, 24, 3840, 48);
                        font-weight: 500;
                        line-height: calc-between-width(1920, 36, 3840, 72);
                        letter-spacing: 0em;
                        text-align: left;
                        text-transform: uppercase;
                        color: #25A8CF;
                       
                    }
                }

                .help-center__faq__question__title__btn {
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: calc-between-width(1920, 40, 3840, 80);
                    height: calc-between-width(1920, 40, 3840, 80);
                    margin-bottom: calc-between-width(1920, 30, 3840, 60);
                    border: 2px solid #25A8CF;
                    border-radius: 100%;
                    background: none;
                    cursor: pointer;

                    &:focus {
                        outline: none;
                    }

                    &::before {
                        position: absolute;
                        content: "";
                        width: calc-between-width(1920, 20, 3840, 40);
                        height: calc-between-width(1920, 2, 3840, 4);
                        background-image: url("data:image/svg+xml,%3Csvg width='20' height='2' viewBox='0 0 20 2' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.450195 1H19.5502' stroke='%2325A8CF' stroke-width='2' stroke-miterlimit='10'/%3E%3C/svg%3E%0A");
                    }

                    svg {
                        width: calc-between-width(1920, 20, 3840, 40);
                        height: calc-between-width(1920, 2, 3840, 4);
                        pointer-events: none;
                        rotate: (90deg);
                        transition: all 0.8s;
                    }

                    .rotate {
                        rotate: (0deg);
                        transition: all .5s;
                    }
                }

                .help-center__faq__question__title__btn:has(.rotate)~.help-center__faq__question__content {
                    color: white;
                    max-height: 1000px;
                    transition: all 1.5s; 
                }
            }


            .help-center__faq__question__content {
                width: 100%;
                margin-left: calc-between-width(1920, 69, 3840, 138);
                margin-right: calc-between-width(1920, 121, 3840, 142);

                max-height: 0;
                transition: all 0.8s;
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 18, 3840, 36);
                font-weight: 400;
                line-height: 32px;
                letter-spacing: 0em;
                text-align: justify;
                color: transparent;

                .help-center__faq__question__content__text{
                    padding-bottom:  calc-between-width(1920, 30, 3840, 80);
                }
            }

        }
    }
}