@import '../../assets/styles/colors.scss';
@import '../../assets/styles/functions.scss';

.footer-section {
    position: relative;
    margin: 0 10%;


    .footer-section__feedback-sections {
        width: 100%;

        .feedback-sections__content {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: calc-between-width(1920, 48, 3840, 96) 0;

            .feedback-sections__text {
                display: flex;
                flex-direction: column;
                max-width: calc-between-width(1920, 675, 3840, 1350);

                .feedback-sections__text-h1 {
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 48, 3840, 96);
                    font-weight: 600;
                    line-height: calc-between-width(1920, 50, 3840, 100);
                    color: white;
                }

                .feedback-sections__text-p {
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 14, 3840, 28);
                    font-weight: 400;
                    margin-bottom: 0;
                    color: white;
                }
            }

            .feedback-sections__form {
                display: flex;
                align-items: center;
                width: auto;

                .feedback-sections__form-input {
                    height: calc-between-width(1920, 64, 3840, 128);
                    width: calc-between-width(1920, 532, 3840, 1064);
                    padding-left: rem(16);
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 16, 3840, 32);
                    border-radius: 60px;
                    background: transparent;
                    border: 1px solid #25A8CF;
                    color: #25A8CF;

                    &:focus {
                        outline: none;

                    }

                    &::placeholder {
                        font-family: 'Poppins';
                        color: #25A8CF;
                    }
                }

                .feedback-sections__form-btn {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-left: 1rem;

                    svg {
                        height: calc-between-width(1920, 16, 3840, 32);
                        width: calc-between-width(1920, 22, 3840, 44);
                        margin-left: 1rem;
                    }
                }
            }
        }

    }

    .footer-section-content {
        padding: rem(76.8) 0 rem(56.7) 0;

        .footer-section-content__columns {
            display: grid;
            grid-template-columns: 5fr 4.4fr 1.5fr 2fr;
            gap: 10%;

            .section__column {
                height: calc-between-width(1920, 251, 3840, 502);

                .column-content {
                    display: flex;
                    align-items: flex-start;
                    flex-direction: column;
                    justify-content: space-between;
                    height: 100%;

                    .column-content__content {

                        .content-logo {
                            display: flex;
                            align-items: center;
                            margin-bottom: 10%;
                            font-family: 'Poppins';
                            font-size: calc-between-width(1920, 24, 3840, 48);
                            font-weight: 600;
                            color: white;

                            svg {
                                width: calc-between-width(1920, 24, 3840, 48);
                                height: calc-between-width(1920, 21, 3840, 42);
                                margin-right: rem(8);
                            }
                        }

                        .content-text {
                            font-family: 'Poppins';
                            text-align: justify;
                            font-size: calc-between-width(1920, 14, 3840, 28);
                            font-weight: 400;
                            color: white;
                        }
                    }

                    .column-content__copyright {
                        font-family: 'Poppins';
                        font-size: calc-between-width(1920, 16, 3840, 32);
                        font-weight: 600;
                        color: white;
                        display: flex;
                        align-items: center;

                        svg {
                            width: calc-between-width(1920, 23, 3840, 46);
                            height: calc-between-width(1920, 24, 3840, 48);
                        }

                        span {
                            font-family: 'Poppins';
                            font-size: calc-between-width(1920, 24, 3840, 48);
                            font-weight: 600;
                            line-height: 33px;
                            letter-spacing: 0em;
                            margin-left: 0.4rem;
                        }
                    }
                }

                .column-contacts {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    height: 100%;

                    h3 {

                        color: white;
                        margin-top: 0;
                        margin-bottom: 2.5rem;
                        font-family: 'Poppins';
                        font-size: calc-between-width(1920, 18, 3840, 36);
                        font-weight: 700;

                    }


                    .column-contacts__items {
                        display: flex;
                        align-items: flex-start;
                        flex-direction: column;
                        justify-content: space-between;
                        height: 100%;
                    }

                    .column-contacts__item {
                        display: flex;
                        align-items: center;

                        .column-contacts__icon {
                            display: flex;
                            align-items: center;
                            margin-right: 1rem;

                            svg {
                                width: calc-between-width(1920, 25, 3840, 50);
                                height: calc-between-width(1920, 25, 3840, 50);
                            }
                        }

                        .column-contacts__text {
                            color: white;
                            font-family: 'Poppins';
                            font-size: calc-between-width(1920, 14, 3840, 28);
                            font-weight: 500;

                            .column-contacts__text-p {
                                margin: 0;
                            }
                        }
                    }
                }

                .column-navigation {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    height: 100%;

                    h3 {
                        margin: 8% 0;
                        margin-top: 0;
                        margin-bottom: 2.5rem;
                        color: white;
                    }

                    .column-navigatin__list-links {
                        list-style-type: disc;
                        font-weight: 500;
                        margin-left: 1.2rem;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        height: 100%;
                        color: white;

                        .column-navigatin-link {
                            a {
                                color: white;
                            }
                        }
                    }

                }

                .column-social {
                    display: flex;
                    justify-content: space-between;
                    flex-direction: column;
                    height: 100%;

                    h3 {
                        margin: 8% 0;
                        margin-top: 0;
                        margin-bottom: 2.5rem;
                        color: white;
                    }

                    .column-social__icons {
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        height: 100%;

                        .column-social__icons__row {
                            display: flex;
                            justify-content: space-between;

                            .social-icon {
                                margin-right: rem(12);
                            }

                            .social-icon:last-child {
                                margin-right: 0;
                            }

                            &:last-child {
                                justify-content: flex-end;

                                .social-icon {
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    width: calc-between-width(1920, 40, 3840, 80);
                                    height: calc-between-width(1920, 40, 3840, 80);
                                    background: #25A8CF;
                                    border-radius: 8px;
                                    cursor: default;

                                    svg {
                                        width: calc-between-width(1920, 20, 3840, 40);
                                        height: calc-between-width(1920, 20, 3840, 40);
                                    }
                                }
                            }
                        }


                        .social-icon {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            width: calc-between-width(1920, 54, 3840, 108);
                            height: calc-between-width(1920, 54, 3840, 108);
                            border-radius: 100%;
                            background: #0D101433;
                            cursor: pointer;

                            svg {
                                width: calc-between-width(1920, 30, 3840, 60);
                                height: calc-between-width(1920, 30, 3840, 60);
                            }
                        }
                    }
                }
            }
        }
    }



}