@import '../../assets/styles/functions.scss';

.blog-page{
    margin: 5% 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .blog-posts{
        display: grid;
        gap: calc-between-width(1920, 16, 3840, 32);
        grid-template-columns: 
        calc-between-width(1920, 475, 3840, 950)
        calc-between-width(1920, 535, 3840, 1070)
        calc-between-width(1920, 475, 3840, 950);
        grid-template-rows: auto auto auto;
        width: 100%;
        .post5, .post9{
            grid-column: span 2;
        }
        

    }

    .blog-buttons{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color:white;
        margin-top: calc-between-width(1920, 64, 3840, 128);

        .blog-buttons__show-more{
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid white;
            border-radius: 30px;
            padding: 1rem 1rem ;
            width: calc-between-width(1920, 178, 3840, 356);
            font-family: 'Poppins';
            font-size: calc-between-width(1920, 16, 3840, 32);
            line-height: calc-between-width(1920, 24, 3840, 48);
            font-weight: 500;
            cursor: pointer;

            svg{
                margin-right: 1rem;
            }
        }
    }

}