@import '../../assets/styles/functions.scss';

.about-us-section {
    margin: 5% 10%;

    .about-us-section__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: calc-between-width(1920, 124, 3840, 248);
        height: auto;
        &:first-child{
            margin-bottom:  calc-between-width(1920, 54, 3840, 108);
        }
        .about-us-section__title-text {
            width: calc-between-width(1920, 630, 3840, 1260);

            .about-us-section__title {
                width: calc-between-width(1920, 547, 3840, 1094);
                position: relative;
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 50, 3840, 100);
                font-weight: 500;
                line-height: calc-between-width(1920, 70, 3840, 140);
                letter-spacing: 0em;
                text-align: left;
                color: white;
                text-transform: uppercase;

                span {
                    position: absolute;
                    width: calc-between-width(1920, 435, 3840, 870);
                    height: calc-between-width(1920, 68, 3840, 136);
                    bottom: -0.5%;
                    left: -2.3%;
                    border-radius: calc-between-width(1920, 40, 3840, 120);
                    border: 1px solid #04ACF5
                }

                .about-us-section__title__decoration {
                    position: absolute;
                    right: 2.5%;
                    bottom: -5%;

                    article {
                        width: calc-between-width(1920, 24, 3840, 48);
                        height: calc-between-width(1920, 48, 3840, 96);
                        margin-left: calc-between-width(1920, 12, 3840, 24);
                    }
                }

            }

            .about-us-section__text {
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 18, 3840, 36);
                font-weight: 400;
                line-height: calc-between-width(1920, 30, 3840, 60);
                letter-spacing: 0em;
                margin-top: calc-between-width(1920, 32, 3840, 64);
                text-align: justify;
                color: white;
            }
        }

        .about-us-section__title-img {
            background: linear-gradient(179.71deg, rgba(170, 57, 249, 0.4) -6.77%, rgba(105, 87, 244, 0) 89.41%);
            border-radius: calc-between-width(1920, 32, 3840, 64);
            img {
                position: relative;
                z-index: -1;
                width: calc-between-width(1920, 768, 3840, 1536);
                height: calc-between-width(1920, 425, 3840, 850);
                border-radius: calc-between-width(1920, 32, 3840, 64);
            }
        }

        .about-us-section__content-img {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            .about-us-section__content-img__first{
                background: linear-gradient(179.71deg, rgba(170, 57, 249, 0.4) -6.77%, rgba(105, 87, 244, 0) 89.41%);
                border-radius: calc-between-width(1920, 32, 3840, 64);
                margin-bottom: calc-between-width(1920, 8, 3840, 20);
                img {
                    position: relative;
                    z-index: -1;
                    width: calc-between-width(1920, 464, 3840, 928);
                    height: calc-between-width(1920, 386, 3840, 772);
                    border-radius: calc-between-width(1920, 32, 3840, 64);
                }
                
            }
            .about-us-section__content-img__second{
                background: linear-gradient(179.71deg, rgba(170, 57, 249, 0.4) -6.77%, rgba(105, 87, 244, 0) 89.41%);
                border-radius: calc-between-width(1920, 32, 3840, 64);
                margin-top: calc-between-width(1920, 8, 3840, 20);
                img {
                    position: relative;
                    z-index: -1;
                    width: calc-between-width(1920, 464, 3840, 928);
                    height: calc-between-width(1920, 300, 3840, 600);
                    border-radius: calc-between-width(1920, 32, 3840, 64);
                }
            }
        }

        .about-us-section__content-text {
            width: calc-between-width(1920, 940, 3840, 1880);
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: space-around;
            height: 100%;
            p {
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 18, 3840, 36);
                font-weight: 400;
                line-height: calc-between-width(1920, 30, 3840, 60);
                letter-spacing: 0em;
                text-align: justify;
                color: white;
                margin-bottom: 0.5rem;
                &:first-child{
                    margin-top: 0;
                }
                &:last-child{
                    margin-bottom: 0;
                }
            }

            
        }


        .bout-us-section__our-team {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 100%;

            .bout-us-section__our-team__title {
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 50, 3840, 100);
                font-weight: 500;
                line-height: calc-between-width(1920, 70, 3840, 140);
                letter-spacing: 0em;
                text-align: center;
                text-transform: uppercase;
                color: white;
                margin-bottom: calc-between-width(1920, 48, 3840, 96);
            }

            .bout-us-section__our-team__members {
                display: flex;
                align-items: center;
                justify-content: space-between;

                .bout-us-section__our-team__member {
                    padding: 0 calc-between-width(1920, 60, 3840, 120);

                    .bout-us-section__our-team__member-img {
                        width: calc-between-width(1920, 300, 3840, 600);
                        height: calc-between-width(1920, 300, 3840, 600);
                        border-radius: 100% 100% 100% 0px;
                        background: #2B2E42;
                        img{
                            width: calc-between-width(1920, 300, 3840, 600);
                            height: calc-between-width(1920, 300, 3840, 600);
                        }
                    }

                    .bout-us-section__our-team__member-info {
                        h4 {
                            margin-top: calc-between-width(1920, 24, 3840, 48);
                            font-family: 'Poppins';
                            font-size: calc-between-width(1920, 24, 3840, 48);
                            font-weight: 600;
                            line-height: calc-between-width(1920, 30, 3840, 60);
                            letter-spacing: 0em;
                            color: white;
                            margin-bottom: 0;

                        }

                        p {
                            font-family: 'Poppins';
                            font-size: calc-between-width(1920, 16, 3840, 32);
                            font-weight: 500;
                            line-height: calc-between-width(1920, 30, 3840, 60);
                            letter-spacing: 0em;
                            color: white;
                            margin: 0;
                        }
                    }
                }
            }
        }

        .about-us-section__achivements-slider{
          
            position: relative;
            display: flex;
            align-items: flex-end;
            justify-content: space-between;
            width: 100%;
            margin-bottom: calc-between-width(1920, 50, 3840, 100);

            .about-us-section__achivements{
                
                .about-us-section__achivements__title{
                    width: calc-between-width(1920, 525, 3840, 1050);              
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 56, 3840, 112);
                    font-weight: 300;
                    line-height: calc-between-width(1920, 64, 3840, 128);
                    letter-spacing: 0em;
                    text-align: left;
                    text-transform: uppercase;
                    color: white;   
                    margin-bottom: calc-between-width(1920, 32, 3840, 64); 
                    
                }
                .about-us-section__achivements__list{

                    .about-us-section__achivements__list__item{
                        width: calc-between-width(1920, 464, 3840, 928);
                        p{
                            margin: 0;
                            font-family: 'Poppins';
                            font-size: calc-between-width(1920, 16, 3840, 32);
                            font-weight: 400;
                            line-height: calc-between-width(1920, 24, 3840, 48);
                            letter-spacing: 0em;
                            text-align: left;
                            color: white;
                            padding-bottom: calc-between-width(1920, 14, 3840, 28);
                            border-bottom: 1px solid #3F3F3F;
                            margin-bottom: calc-between-width(1920, 23, 3840, 46);
                        }

                        &:last-child p{
                            margin-bottom: 0;

                        }
                    }
                }
            }

            .about-us-section__slider{
                position: relative;
                width: calc-between-width(1920, 1000, 3840, 2000);
                height: calc-between-width(1920, 380, 3840, 760);
            }
        }
    }
}