@import '../../assets/styles/functions.scss';

.producst__table{
    position: relative;
    border-collapse:collapse;
    width: 100%;
   
    .producst__table__thead{
       
        tr{
            background: #25A8CF;
            width: 100%;
            height: calc-between-width(1920, 44, 3840, 88);   

            td{  
                border-right: calc-between-width(1920, 1, 3840, 2)   solid #19151E;
                &:first-child{
                    border-top-left-radius: calc-between-width(1920, 8, 3840, 16);  
                }
                &:last-child{             
                    border-top-right-radius: calc-between-width(1920, 8, 3840, 16);  
                    border-width: 0;
                }

                .thead__td__header{
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: auto;
                    color:#19151E;
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 18, 3840, 36);
                    font-weight: 600;
                    line-height: calc-between-width(1920, 27, 3840, 54);
                    padding: 0 calc-between-width(1920, 16, 3840, 32);
                    letter-spacing: 0em;
                    .thead__header__btns{
                        
                        margin-left: calc-between-width(1920, 6, 3840, 12);
                        button{
                            position: absolute;
                            border: none;
                            background: transparent;
                            padding: 0;
                            svg{
                                width: calc-between-width(1920, 7, 3840, 14);
                                height: calc-between-width(1920, 6, 3840, 12);
                            }
                        }
                        .thead__header__btns__up{
                            top:calc-between-width(1920, 0, 3840, 12);
                        }
                        .thead__header__btns__down{
                            bottom:calc-between-width(1920, 4, 3840, 12);
                        }
                    }
                }
            }
        }
    }
    .producst__table__tbody{
        tr{
            td{
                color: white;
                padding: 0 calc-between-width(1920, 12, 3840, 24);
                height: calc-between-width(1920, 52, 3840, 108);
                border-right: calc-between-width(1920, 1, 3840, 2)   solid rgba(37, 168, 207, 0.4);
                font-size: calc-between-width(1920, 16, 3840, 32);  
                text-align: center;
                &:first-child{
                    border-top-left-radius: calc-between-width(1920, 8, 3840, 16);  
                }
                &:last-child{             
                    border-top-right-radius: calc-between-width(1920, 8, 3840, 16);  
                    border-width: 0;
                }
            }
            &:nth-child(even) {
                background: rgba(37, 168, 207, 0.2)

            }

            &:last-child{
                border-bottom: calc-between-width(1920, 1, 3840, 2)   solid rgba(37, 168, 207, 0.4);
            }
        }

    }
}