@import "../../assets/styles/colors.scss";
@import "../../assets/styles/functions.scss";

.crypto-statistics-wrapper {



}

.running-line__element {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: calc-between-width(1920, 13, 3840, 26);
  width: auto;

  .running-line__name {
    font-family: 'Poppins';
    font-size: calc-between-width(1920, 14, 3840, 28);
    font-weight: 700;
    color: #25A8CF;
    text-transform: uppercase;
    white-space: nowrap;
  }

  .running-line__price {
    color: #E5EFEF;
    font-family: 'Poppins';
    font-size: calc-between-width(1920, 14, 3840, 28);
    font-weight: 700;
    margin: 0 0.5rem;
  }

  .running-line__discount {
    color: #25A8CF;
    font-family: 'Poppins';
    font-size: calc-between-width(1920, 14, 3840, 28);
    font-weight: 700;
  }

  &::after {
    content: "";
    width: calc-between-width(1920, 5, 3840, 10);
    height: calc-between-width(1920, 5, 3840, 10);
    background: #E5EFEF;
    border-radius: 100px;
    margin: 0 2rem;
  }

}