a{
  text-decoration: none;
  color:rgb(54, 50, 50);
}

ul{
  text-decoration: none;
  list-style: none;
  padding: 0;
  margin: 0;
}



.overlay{
  position: fixed;
  z-index: 99;
  top:0;
  left: auto;
  right: auto;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(6px);
}

.section-h1{
  font-family: 'Poppins';
  margin-top: 0;
  margin-bottom: 0;
  font-size: calc-between-width(1920, 50, 3840, 100);
  font-weight: 600;
  line-height: 70px;
}

.section-p{
  font-family: 'Poppins';
  font-size: calc-between-width(1920, 18, 3840, 36);
  font-weight: 400;
  line-height: 30px;
  margin-top: 0;
  margin-bottom: rem(96);
  color:#4F5665;
}

.section-btn-gradient{
  font-family: 'Poppins';
  font-size: calc-between-width(1920, 16, 3840, 32);
  font-weight: 500;
  color: #fff;
  background: linear-gradient(93.9deg, #25A8CF -23.33%, #8540BB 154.23%);
  border:1px solid transparent;
  border-radius:60px;
  width: calc-between-width(1920, 250, 3840, 500);
  height: calc-between-width(1920, 64, 3840, 128);
  cursor: pointer;
}

.section-link{
  
  display: flex;
  align-items: center;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid black;
  a{
      font-family: 'Poppins';
      color: black;
  }
  svg{
      margin-left: 0.5rem;
  }
}


.unselectable {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */
  -khtml-user-select: none;    /* Konqueror */
  -moz-user-select: none;      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge */
  user-select: none;           /* Non-prefixed version, currently
                                  not supported by any browser */
}

::-webkit-scrollbar {
  width: 14px; /*    */
} 
::-webkit-scrollbar-track {
  background: #DBDBDB; /*     */
  box-shadow: 0 0 2px rgba(0, 0, 0, .2) inset; /*    */
}
::-webkit-scrollbar-thumb {
  background: #C2BEBE; /*     */
  border-radius: 8px; /*   */
  border: 3px solid #DBDBDB; /* ,     */
}



::-webkit-scrollbar-thumb:hover {
  background: #767676; /*      */
}


/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}