@charset 'UTF-8';
@import '../../../assets/styles/functions.scss';

.about-us-section__slider {
    position: relative;

    /* Slider */
    .slick-loading .slick-list {
        background: #fff center center no-repeat;
    }



    /* Arrows */
    .slick-prev,
    .slick-next {
        font-size: 0;
        line-height: 0;

        position: absolute;
        display: block;
        z-index: 10;
        width: 40px;
        height: 40px;
        padding: 0;
        -webkit-transform: translate(0, -50%);
        -ms-transform: translate(0, -50%);
        transform: translate(0, -50%);

        cursor: pointer;

        color: transparent;
        border: none;
        outline: none;
        background: transparent;
    }

    .slick-prev:hover,
    .slick-prev:focus,
    .slick-next:hover,
    .slick-next:focus {
        color: transparent;
        outline: none;
        background: transparent;
    }

    .slick-prev:hover:before,
    .slick-prev:focus:before,
    .slick-next:hover:before,
    .slick-next:focus:before {
        opacity: 1;
    }

    .slick-prev.slick-disabled:before,
    .slick-next.slick-disabled:before {
        opacity: .25;
    }

    .slick-prev:before,
    .slick-next:before {
        font-family: 'slick';
        font-size: 20px;
        line-height: 1;

        display: block;
        width: 40px;
        height: 40px;

        color: black;

        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }

    .slick-prev {
        top: -13.8%;
        right: 8%;
    }



    .slick-prev:before {
        content: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20 37.5C29.665 37.5 37.5 29.665 37.5 20C37.5 10.335 29.665 2.5 20 2.5C10.335 2.5 2.5 10.335 2.5 20C2.5 29.665 10.335 37.5 20 37.5Z' stroke='%23746A81' stroke-width='1.5' stroke-miterlimit='10'/%3E%3Cpath d='M29.5502 20H10.4502' stroke='%23746A81' stroke-width='1.5' stroke-miterlimit='10'/%3E%3Cpath d='M18.4002 27.95L10.4502 20L18.4002 12.05' stroke='%23746A81' stroke-width='1.5' stroke-miterlimit='10'/%3E%3C/svg%3E%0A");
    }


    .slick-next {
        top: -13.8%;
        right: 2%;
    }


    .slick-next:before {
        content: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20 37.5C10.335 37.5 2.5 29.665 2.5 20C2.5 10.335 10.335 2.5 20 2.5C29.665 2.5 37.5 10.335 37.5 20C37.5 29.665 29.665 37.5 20 37.5Z' stroke='%23746A81' stroke-width='1.5' stroke-miterlimit='10'/%3E%3Cpath d='M10.4498 20H29.5498' stroke='%23746A81' stroke-width='1.5' stroke-miterlimit='10'/%3E%3Cpath d='M21.5998 27.95L29.5498 20L21.5998 12.05' stroke='%23746A81' stroke-width='1.5' stroke-miterlimit='10'/%3E%3C/svg%3E%0A");
    }


    /* Dots */
    .slick-dotted.slick-slider {
        margin-bottom: 30px;
    }

    .slick-dots {
        position: absolute;
        bottom: -60px;

        display: flex !important;
        align-items: center;
        justify-content: center;

        width: 100%;
        padding: 0;

        list-style: none;
    }

    .slick-dots li {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;

        width: auto;
        height: 15px;
        margin: 0 7.5px;
        padding: 0;

        cursor: pointer;
    }

    .slick-dots li button {
        display: flex;
        align-items: center;
        justify-content: center;
        color: transparent;
        background: #DDE0E4;
        width: 15px;
        height: 15px;
        padding: 5px;
        border: 0;
        border-radius: 100px;
        cursor: pointer;

        transition: opacity .5s, width .5s;
        transition-delay: .5s, 0s;
    }


    .slick-dots li.slick-active button {
        width: 45px;
        height: 15px;
        background: #25A8CF;
        transition: opacity .5s, background-color .5s, width .5s;
        transition-delay: .5s, 0s, 0s;

    }

}