@import '../../assets/styles/colors.scss';
@import '../../assets/styles/functions.scss';

.send-message-section{
    margin: 5% 10%;
    margin-bottom: 0;
    text-align: center;
    .send-message-section__content{
        .send-message-section__content-title{
            .content-title__h1{
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 64, 3840, 128);
                font-weight: 500;
                line-height: 96px;
                letter-spacing: 0em;
                text-align: center;
                color: white;
            }
            .content-title__p{
                color: white;
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 16, 3840, 32);
                font-weight: 400;
                line-height: 30px;
                letter-spacing: 0em;
                text-align: center;
                margin-bottom: 1.5rem;
            }
            .content-title__button{
                display: flex;
                align-items: center;
                justify-content: center;
                margin: auto auto;
                background:none;
                border: 1px solid #25A8CF;
                background: #25A8CF;
                border-radius: 60px;
                height: calc-between-width(1920, 56, 3840, 112);
                min-width: calc-between-width(1920, 56, 3840, 112);
                color:white;
                font-size: calc-between-width(1920, 16, 3840, 32);
                padding: calc-between-width(1920, 16, 3840, 32) calc-between-width(1920, 12, 3840, 24);
                margin-bottom: 1rem;
                cursor: pointer;
                svg{
                    height: calc-between-width(1920, 25, 3840, 50);
                    width: calc-between-width(1920, 25, 3840, 50);
                    path{
                        stroke:white;
                    }
                }
                span{
                    display: flex;
                    align-items: center;
                    font-family: 'Poppins';
                    font-size:calc-between-width(1920, 16, 3840, 32);
                    margin-left: 0.5rem;
                }
            }
        }
        .send-message-section__content-form{
            overflow: hidden;
            .content-form__inputs{
                display: flex;
                flex-direction: column;
                align-items: center;
                input{
                    width: calc-between-width(1920, 620, 3840, 1240);
                    margin: 1rem 0;
                    padding: calc-between-width(1920, 16, 3840, 32);
                    border:1px solid #4F5665; 
                    border-radius: 40px;
                    font-size: calc-between-width(1920, 16, 3840, 32);
                    &::placeholder{
                        font-family: 'Poppins';
                        font-size: calc-between-width(1920, 16, 3840, 32);
                    }
                    &:focus{
                        outline: none;
                    }
                }
                textarea{
                    margin: 1rem 0;
                    padding: 16px;
                    min-width: calc-between-width(1920, 620, 3840, 1240);
                    max-width: calc-between-width(1920, 620, 3840, 1240);
                    min-height: calc-between-width(1920, 140, 3840, 280);
                    max-height: calc-between-width(1920, 140, 3840, 280);;
                    border:1px solid #4F5665; 
                    border-radius: 16px;
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 16, 3840, 32);
                    &::placeholder{
                        font-family: 'Poppins';
                        font-size: calc-between-width(1920, 16, 3840, 32);
                    }
                    &:focus{
                        outline: none;
                    }
                }
            }
        }
        .form-is-close{
            position: relative;
            height: 0;
            opacity: 0;
            transition: opacity .15s, height .3s linear;
            transition-delay: 0s, 0s;
            z-index: -1;
        }

        .form-is-open{
            height: calc-between-width(1920, 610, 3840, 1220);
            transition: opacity .3s, height .3s linear;
            transition-delay: .2s, 0s;
        }
    }
}