@import "../../assets/styles/functions.scss";

.template-blog-post {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    background: #E9ECEF;
    border-radius: calc-between-width(1920, 16, 3840, 32);
    width: 100%;
    height: calc-between-width(1920, 415, 3840, 830);
    cursor: pointer;

    .template-blog-post__container {
        position: relative;
        border-radius: calc-between-width(1920, 16, 3840, 32);
        width: 100%;
        .template-blog-post__blur{
            position: absolute;
            background: #CDD8DA73;
            width: 100%;
            height: 100%;
            z-index: 1;
            border-radius: calc-between-width(1920, 16, 3840, 32);
            overflow: hidden;
            backdrop-filter: blur(4px);
        }

        .template-blog-post__info {
            position: relative;
            z-index: 2;
            margin: calc-between-width(1920, 24, 3840, 50) calc-between-width(1920, 21, 3840, 50);
            .template-blog-post__icons {
                svg {
                    width: calc-between-width(1920, 25, 3840, 50);
                    height: calc-between-width(1920, 25, 3840, 50);
                    margin-right: 1.5rem;
                }

            }

            .template-blog-post__title {
                margin: calc-between-width(1920, 8, 3840, 16) 0;
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 24, 3840, 48);
                font-weight: 600;
                line-height: calc-between-width(1920, 36, 3840, 72);
                letter-spacing: 0em;
                text-align: left;
                max-width: calc-between-width(1920, 380, 3840, 760);
                color: #0E0E0F;
            }

            .template-blog-post__creator {
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 16, 3840, 32);
                font-weight: 500;
                line-height: calc-between-width(1920, 24, 3840, 48);
                letter-spacing: 0em;
                text-align: left;
                color: #0E0E0F;
            }
        }

    }


}