@import '../../assets/styles/colors.scss';
@import '../../assets/styles/functions.scss';

.header {
    position: relative;

    .nav-links {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: rem(140);
        margin: 0 10%;
        margin-top: 1%;
        .logo {
            a {
                display: flex;
                align-items: center;
                color: white;
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 24, 3840, 48); 
                font-weight: 500;
                line-height: 36px;
                letter-spacing: 0em;
                text-align: left;

                svg {
                    width: calc-between-width(1920, 23, 3840, 46); 
                    height: calc-between-width(1080, 20, 2160, 40); 
                    margin-right: rem(8);
                }
            }
        }

        .default-links {
            position: relative;

            .header-list-links {
                display: flex;
                align-items: center;
                list-style: none;

                .list-link {
                    margin: 0 rem(16);
                    cursor: pointer;
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 16, 3840, 32);
                    font-weight: 500;

                    .active {
                        color: #fff;
                        border-bottom: 2px solid rgba(37, 168, 207, 1);
                    }

                    a {
                        color: #fff;
                        display: flex;
                        align-items: center;
                        border-bottom: 2px solid transparent;
                        padding: 0.3rem 0;
                        white-space: nowrap;

                        svg {
                            width: calc-between-width(1920, 10, 3840, 20);
                            height: calc-between-width(1080, 7, 2160, 14); 
                            margin-left: rem(4);
                            path {
                                stroke: #fff;
                            }

                        }
                    }

                }
            }

        }

        .header__search {
            display: flex;
            align-items: center;
            margin-left: rem(80);
            margin-right: rem(4.8);
            width: 100%;

            .header__search-input {
                width: 100%;
                border: none;
                border-bottom: 1px solid white;
                color:white;
                background-color: transparent;
                height: calc-between-width(1080, 24, 2160, 48);
                
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 16, 3840, 32);
                font-weight: 400;

                &:focus {
                    outline: none;
                }

                &::placeholder {
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 16, 3840, 32);
                    font-weight: 400;
                    color:white;
                }
            }
        }

        .icons-action {
            display: flex;
            align-items: center;

            .icons-action__element {
                display: flex;
                align-items: center;
                margin: 0 rem(8);
                cursor: pointer;

                svg {
                    width: calc-between-width(1920, 41, 3840, 82);
                    height: calc-between-width(1080, 41, 2160, 82); 
                    path{
                        stroke:white;
                    }
                    rect{
                        stroke: white;
                    }
                }
            }

            .icons-action__element-theme {
                display: flex;
                align-items: center;
                border: 1px solid transparent;
                background: transparent;
                border-radius: 60px;
                padding: 0 16px;
                font-size: calc-between-width(1920, 16, 3840, 32);
                cursor: pointer;
                color: #fff;
                svg {
                    width: calc-between-width(1920, 24, 3840, 48);
                    height: calc-between-width(1080, 24, 2160, 48); 
                }
            }
        }
    }
}



.header__search:not(.is-active) {
    pointer-events: none;
    display: none !important;
}

.is-active {
    opacity: 1 !important;
}

.display-none {
    display: none !important;
}