@import '../../assets/styles/colors.scss';
@import '../../assets/styles/functions.scss';

.products-section {
    position: relative;
    margin: 0 10%;
    margin-top: 5%;
    font-family: 'Poppins';

    .products-section-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 4%;
        width: 100%;


        .products-section-top__title {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            font-family: 'Poppins';
            font-size: calc-between-width(1920, 48, 3840, 96);
            font-weight: 700;
            line-height: calc-between-width(1920, 40, 3840, 80);
            margin-left: 1rem;
            color: white;
            padding-left: calc-between-width(1920, 16, 3840, 32);
            border-left: calc-between-width(1920, 6, 3840, 12) solid white;

            .products-section-top__title__sub-text {
                margin-top: 0.5rem;
                font-size: calc-between-width(1920, 18, 3840, 36);
                font-weight: 400;
                line-height: calc-between-width(1920, 16, 3840, 32);
                color: #706E6E;
            }
        }

        .products-section-top__product-list-action{
            display: flex;

            .products-section-top__sort {
                display: flex;
                align-items: center;
                
                font-size: calc-between-width(1920, 18, 3840, 36);
                font-weight: 400;
                line-height: calc-between-width(1920, 21, 3840, 42);
                letter-spacing: 0em;
                color: white;
            }
    
            .products-section-top__table {
                margin-left: calc-between-width(1920, 24, 3840, 48);
                .products-section-top__table-btn {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background: transparent;
                    border: 1px solid white;
                    border-radius: 100%;
                    width: calc-between-width(1920, 58, 3840, 116);
                    height: calc-between-width(1920, 58, 3840, 116);
                    cursor: pointer;
                    svg {
                        width: calc-between-width(1920, 25, 3840, 50);
                        height: calc-between-width(1920, 25, 3840, 50);
                    }
                }
            }
        }
 
    }

    .products-section-content {
        position: relative;
        display: flex;
        align-items: flex-start;
        margin-bottom: 10%;

        .products-section-content__filters {
            margin-right: calc-between-width(1920, 16, 3840, 32);
        }

        .products-section-content__cards {
            display: grid;
            grid-template-rows: auto auto auto;
            grid-template-columns: auto auto auto;
            gap: calc-between-width(1920, 16, 3840, 32);
        }

        .products-section-content__table{
            position: relative;
            width: 100%;
        }

    }
}