@import '../../assets/styles/functions.scss';

.contacts-sectino {
    position: relative;
    margin: 5% 10%;
    margin-bottom: 8%;

    .contacts-sectino__info {
        .contacts-sectino__title {
            font-family: 'Poppins';
            font-size: calc-between-width(1920, 64, 3840, 128);
            font-weight: 400;
            line-height: calc-between-width(1920, 84, 3840, 168);
            letter-spacing: 0em;
            text-align: left;
            text-transform: uppercase;
            color: white;
            margin-bottom: calc-between-width(1920, 32, 3840, 64);
        }

    }

    .contacts-sectino__content {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .contacts-sectino__content__contact {

            input {
                width: calc-between-width(1920, 448, 3840, 896);
                height: calc-between-width(1920, 70, 3840, 140);
                padding: 0px calc-between-width(1920, 16, 3840, 32);
                background: transparent;
                border-radius: calc-between-width(1920, 60, 3840, 120);
                border: 1px solid #25A8CF;
                color: #25A8CF;
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 16, 3840, 32);
                font-weight: 500;
                line-height: calc-between-width(1920, 24, 3840, 48);
                letter-spacing: 0em;
                margin-bottom:  calc-between-width(1920, 30, 3840, 60);

                &::placeholder {
                    color: #746A81;
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 16, 3840, 32);
                    font-weight: 600;
                    line-height: calc-between-width(1920, 24, 3840, 48);
                }

                &:focus {
                    outline: none;
                }
            }

            textarea {
                width:  calc-between-width(1920, 448, 3840, 896);
                height:  calc-between-width(1920, 188, 3840, 376);
                resize: none;
                margin-bottom: calc-between-width(1920, 30, 3840, 60);
                padding: calc-between-width(1920, 16, 3840, 32);
                background: transparent;
                border-radius: calc-between-width(1920, 16, 3840, 32);
                border: 1px solid #25A8CF;
                color: #25A8CF;
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 16, 3840, 32);
                font-weight: 500;
                line-height: calc-between-width(1920, 24, 3840, 48);
                letter-spacing: 0em;

                &::placeholder {
                    color: #746A81;
                    font-family: 'Poppins';
                    font-size: calc-between-width(1920, 16, 3840, 32);
                    font-weight: 600;
                    line-height: calc-between-width(1920, 24, 3840, 48);
                }

                &:focus {
                    outline: none;
                }
            }

            .section-btn-gradient {
                width:  calc-between-width(1920, 480, 3840, 960);
                height: calc-between-width(1920, 70, 3840, 140);
                
                font-family: 'Poppins';
                font-size: calc-between-width(1920, 20, 3840, 40);
                font-weight: 500;
                line-height: calc-between-width(1920, 31, 3840, 62);
                letter-spacing: 0em;
                text-align: center;

            }
        }

        .contacts-sectino__content__map {
            width: calc-between-width(1920, 960, 3840, 1920);
            height: calc-between-width(1920, 540, 3840, 1080);

            border-radius: 8px;

            iframe {
                width: 99.5%;
                height: 99%;
            }
        }
    }

}